import CloseIcon from 'vue-material-design-icons/Close.vue';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import { loadState } from '@nextcloud/initial-state';
import { generateUrl } from '@nextcloud/router';
import axios from '@nextcloud/axios';
import { delay } from '../utils.js';
import { showSuccess, showError } from '@nextcloud/dialogs';
export default {
  name: 'DocuSignSettings',
  components: {
    NcButton,
    CloseIcon
  },
  props: [],
  data() {
    return {
      state: loadState('integration_docusign', 'docusign-config'),
      // to prevent some browsers to fill fields with remembered passwords
      readonly: true,
      loading: false,
      redirect_uri: window.location.protocol + '//' + window.location.host + generateUrl('/apps/integration_docusign/docusign/oauth-redirect')
    };
  },
  computed: {
    oAuthConfigured() {
      return this.state.docusign_client_id && this.state.docusign_client_secret;
    },
    connected() {
      return this.state.docusign_token && this.state.docusign_token !== '';
    }
  },
  watch: {},
  mounted() {
    const paramString = window.location.search.slice(1);
    // eslint-disable-next-line
    const urlParams = new URLSearchParams(paramString);
    const dsToken = urlParams.get('docusignToken');
    if (dsToken === 'success') {
      showSuccess(t('approval', 'Successfully connected to DocuSign!'));
    } else if (dsToken === 'error') {
      showError(t('approval', 'OAuth access token could not be obtained:') + ' ' + urlParams.get('message'));
    }
  },
  methods: {
    onFieldInput() {
      this.loading = true;
      delay(() => {
        this.saveOptions({
          docusign_client_id: this.state.docusign_client_id,
          docusign_client_secret: this.state.docusign_client_secret
        });
      }, 2000)();
    },
    saveOptions(values) {
      const req = {
        values
      };
      const url = generateUrl('/apps/integration_docusign/docusign-config');
      axios.put(url, req).then(response => {
        showSuccess(t('approval', 'DocuSign admin options saved'));
      }).catch(error => {
        showError(t('approval', 'Failed to save DocuSign admin options') + ': ' + error.response.request.responseText);
      }).then(() => {
        this.loading = false;
      });
    },
    onOAuthClick() {
      const oauthState = Math.random().toString(36).substring(3);
      const scopes = ['signature', 'user_read', 'account_read'];
      const requestUrl = 'https://account-d.docusign.com/oauth/auth' + '?client_id=' + encodeURIComponent(this.state.docusign_client_id) + '&redirect_uri=' + encodeURIComponent(this.redirect_uri) + '&response_type=code' + '&state=' + encodeURIComponent(oauthState) + '&scope=' + scopes.join(',');
      const req = {
        values: {
          docusign_oauth_state: oauthState,
          docusign_redirect_uri: this.redirect_uri
        }
      };
      const url = generateUrl('/apps/integration_docusign/docusign-config');
      axios.put(url, req).then(response => {
        window.location.replace(requestUrl);
      }).catch(error => {
        showError(t('approval', 'Failed to save DocuSign OAuth state') + ': ' + error.response.request.responseText);
      }).then(() => {});
    },
    onLogoutClick() {
      this.state.docusign_token = '';
      this.saveOptions({
        docusign_token: this.state.docusign_token
      });
    }
  }
};